import React, { ReactElement, useEffect, useMemo } from "react";
import { HashRouter as Router } from "react-router-dom";

import { AuthManager } from "./AuthManager";
import { Content } from "./Content";
import { Navigation } from "./Navigation";
import { Sidebar } from "./Sidebar";
import { AuthState, ChildrenArgs, JobState } from "./types";
import { useWorkbenchState } from "./useWorkbenchState";

import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.scss";

type Props = Readonly<{
  blockingStep: string;
  children: (args: ChildrenArgs) => ReactElement;
  loadRoles: Array<string>;
  settings: ReactElement | undefined;
  workbenchTitle: string;
}>;

export const Workbench = ({
  blockingStep,
  children,
  loadRoles,
  settings,
  workbenchTitle,
}: Props) => {
  const [state, actions] = useWorkbenchState(blockingStep);
  const jobTitle = useMemo(() => {
    if (state.job.state === JobState.ready && state.job.job.name && state.job.job.name.length > 0) {
      return state.job.job.name;
    }
    return null;
  }, [state.job.state, workbenchTitle]);
  useEffect(() => {
    if (jobTitle && jobTitle.length > 0) {
      document.title = `${workbenchTitle} - ${jobTitle}`;
    } else {
      document.title = workbenchTitle;
    }
  }, [jobTitle, workbenchTitle]);
  const visible =
    state.sidebarVisible ||
    state.auth.state === AuthState.notLoggedIn ||
    state.auth.state === AuthState.loggingInByForm;
  return (
    <Router>
      <AuthManager actions={actions} state={state}>
        <>
          <Navigation
            jobTitle={jobTitle}
            showProductionAlert={
              state.auth.state === AuthState.loggedIn && state.auth.activeKey === "live"
            }
            title={workbenchTitle}
            toggleSidebar={actions.toggleSidebar}
          />
          <main>
            <Content
              actions={actions}
              loadRoles={loadRoles}
              render={children}
              sidebarVisible={visible}
              state={state}
            />
            <Sidebar
              actions={actions}
              blockingStep={blockingStep}
              customSettings={settings}
              state={state}
            />
          </main>
        </>
      </AuthManager>
    </Router>
  );
};
Workbench.displayName = "Workbench";
