import React from "react";
import { Link } from "react-router-dom";
import { Alert, Button, Nav, Navbar, NavbarText, NavItem } from "reactstrap";

type Props = Readonly<{
  jobTitle: string | null;
  showProductionAlert: boolean;
  title: string;
  toggleSidebar: () => void;
}>;

export const Navigation = ({ jobTitle, showProductionAlert, title, toggleSidebar }: Props) => {
  return (
    <Navbar color="dark" dark>
      <Nav>
        <NavItem className="mr-4">
          <Button color="light" onClick={toggleSidebar} outline type="button">
            <i className="fa fa-bars" />
          </Button>
        </NavItem>
        <Link className="navbar-brand" to="/" title={title}>
          {title}
        </Link>
      </Nav>
      {jobTitle && (
        <NavbarText className="border-left border-white pl-3 text-white">{jobTitle}</NavbarText>
      )}
      <Nav className="ml-auto">
        <NavItem>
          <Alert color="warning" className="mb-0" fade isOpen={showProductionAlert}>
            <i className="fa fa-warning mr-2" />
            <strong>The workbench is showing jobs used to train the predictive models.</strong>
          </Alert>
        </NavItem>
      </Nav>
    </Navbar>
  );
};
Navigation.displayName = "Navigation";
